.row{
    margin-left: 20px;
    color: aliceblue;

}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    max-height: 250px;
    margin-right:10px ;
    cursor: pointer;
}
.smallPosters{
    max-height: 150px;
    margin-right:10px ;
    cursor: pointer;
}
.poster:hover{
    transform-style: inherit;
    transform: scale(1.2);
}
.smallPosters:hover{
    transform: scale(1.2);
}